import { createApp, defineAsyncComponent } from 'vue';
import qs from 'qs';

import Core from '@tmagic/core';
import { getUrlParam } from '@tmagic/utils';

import components from '../.tmagic/async-comp-entry';
import plugins from '../.tmagic/plugin-entry';

import request from './utils/request';
import AppComponent from './App.vue';
import { getLocalConfig } from './utils';

const magicApp = createApp(AppComponent);

magicApp.use(request);

Object.entries(components).forEach(([type, component]: [string, any]) => {
  magicApp.component(`magic-ui-${type}`, defineAsyncComponent(component));
});

Object.values(plugins).forEach((plugin: any) => {
  magicApp.use(plugin);
});

const fetchData = async () => {
  const parse = qs.parse(window.location.search);
  const res = await fetch(`/api/act/get?id=${parse.id}`, {
    headers: {
      Accept: 'application/json',
    },
  });
  const body = await res.json();
  // @ts-ignore
  // eslint-disable-next-line no-eval
  window.magicDSL = eval(`(${body.data.pages[0].distCode})`);
};

const main = async () => {
  await fetchData();
  const app = new Core({
    ua: window.navigator.userAgent,
    // @ts-ignore
    config: window.magicDSL || {},
    curPage: getUrlParam('page'),
  });

  app.setDesignWidth(app.env.isWeb ? window.document.documentElement.getBoundingClientRect().width : 375);

  magicApp.config.globalProperties.app = app;
  magicApp.provide('app', app);

  magicApp.mount('#app');
};

main();
